/* Navbar.css */
.navbar {
  background-color: #333;
  overflow: hidden;
  padding: 1rem;
}

.navbar h2 {
  display: inline;
  color: white;
  margin-right: 2rem;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: inline;
}

.navbar li {
  display: inline;
  margin-right: 1rem;
}

.navbar li a {
  color: white;
  text-decoration: none;
  padding: 8px 16px;
}

.navbar li a:hover {
  background-color: #ddd;
  color: black;
}

/* Navbar.css */
.logout-button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  text-decoration: underline;
}

.logout-button:hover {
  color: #ddd;
}
